/*Retailer Portal ***********************************************************************************************/
@media (max-width: 1160px) {
    .form_brands-left {
        max-width: 40%;
    }
    .popup-invite .popup_content {
        max-width: 98%;
    }
    /*
        .content,.wrapper{
        overflow-y: auto;
    overflow-x: hidden;
    }
*/
    .brands-single-page {
        overflow-y: auto;
        overflow-x: hidden;
    }

    .cart-right h2 span {
        display: inline-block;
    }
    .my_list-product-title {
        min-width: 15rem;
        display: inline-block;
    }

    .popup-invite .form_brands-left {
        max-width: 40.1rem;
    }
    .popup-invite .form_brands {
        gap: 1rem;
    }
}

@media (max-width: 992px) {
    .header,
    .bottom-notify {
        z-index: 11;
    }
    .header_container {
        flex-wrap: wrap;
    }
    .header_logo {
        order: 1;
    }
    .header_menu-wrap {
        order: 4;
        flex-basis: 100%;
        margin-top: 0;
    }
    .header_search.search {
        order: 3;
    }

    .header_block-bottom {
        margin-top: 8rem;
    }
    .header_actions {
        position: relative;
        display: flex;
        flex-wrap: nowrap;
        order: 2;
    }
    .content {
        padding-top: 13rem;
        padding-bottom: 5rem;
    }
    .content.setting-section {
        padding-top: 12rem;
    }
    .form_brands {
        flex-direction: column;
    }
    .form_brands-left {
        max-width: 100%;
        padding-top: 40px;
        margin-bottom: 0;
    }
    .brand-single_head-content {
        flex-direction: column;
        align-items: flex-start;
        align-content: flex-start;
    }

    .cart-information {
        flex-direction: column;
        gap: 1rem;
    }
    .cart-left,
    .cart-right {
        max-width: 100%;
    }

    .setting-section .pc_tabs.tabs {
        height: auto;
        flex-direction: column;
        max-height: inherit;
    }
    .setting-section .filters {
        width: 100%;
    }

    .setting-section .pc_tabs-content.tabs_body {
        width: 100%;
        max-height: 100%;
    }
    .setting-section aside.filters {
        position: relative;
        left: auto;
    }
    .setting-section .products_head,
    .setting-section .mp-filter .filters_block {
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .setting-section .filters_block {
        width: 100%;
    }
    .setting-section .tabs .tab_menu {
        clear: both;
    }

    .tabs-hamburger-lines {
        position: absolute;
        top: 22px;
        right: 0;
    }

    .setting-section .filter_body {
        height: auto !important;
    }
    .setting-section .tabs .tab_menu {
        display: none;
    }
    .setting-section
        .filters
        .tab_wrap
        input[type='checkbox']:checked
        ~ .pc_tabs-menu {
        display: flex;
    }
    .setting-section.mp-content .user_info .datalist-table_wrap {
        overflow: auto;
    }
    .hamburger-lines,
    .tabs-hamburger-lines {
        display: flex;
    }
    .filters:after {
        display: none;
    }
    .popup-invite .form_brands-left {
        max-width: 100%;
    }
    .brand-single_content-body {
        gap: 2rem;
        flex-direction: column;
    }
    .brand-single_about {
        max-width: 100%;
        min-width: 100%;
    }
    .products_head-content .title h1 {
        font-size: 2.4rem;
    }
    .checkout-info .cart-left,
    .checkout-info .cart-right {
        max-width: 100%;
    }
}

@media (max-width: 768px) {
    :root {
        --headings-sizes-h1: 3rem;
    }
    .header_search.search {
        flex-basis: 100%;
    }
    .content,
    .content.mp-content {
        padding-top: 15rem;
    }
    .header {
        height: auto;
    }
    .header_block-top {
        padding-left: 1rem;
    }
    .header_container {
        gap: 0.5rem;
    }
    .logo img {
        width: 70%;
    }
    .products_head-content h1 {
        font-size: 2.4rem;
    }

    .products_body {
        padding: 10px;
    }
    .pagination {
        flex-wrap: wrap;
        gap: 2rem;
    }
    .products_head-search {
        flex: none;
        width: 100%;
    }
    :root {
        --headings-sizes-h1: 2rem;
        --headings-sizes-h2: 1.8rem;
    }
    .popup-message .popup_content,
    .popup-invite .popup_content {
        margin: 10px;
    }
    .popup_success {
        width: 100%;
    }
    .popup-message .popup_content {
        max-width: initial;
    }
    .setting-popup .action-footer {
        flex-direction: column;
    }
    .setting-popup .action-footer .button {
        width: 100%;
        height: auto;
    }
    .setting-section .datalist-table .button {
        margin-bottom: 1rem;
    }
    .choose-billing-method .radiobox-text {
        padding-right: 5.5rem;
    }
    .note-flex svg.icon {
        flex: 0 0 2.5rem;
    }
    .popup_success {
        width: 100%;
        padding: 5rem 3rem;
    }
    .brands-single-page main.content {
        overflow-x: hidden;
    }
    .products--style-1 .products_body {
        padding-right: 1rem;
    }
    .product_head .view-list {
        margin-left: 0;
    }
    .product {
        padding: 2.4rem 1.5rem 2.4rem 1.5rem;
    }
    .product_details-wish {
        flex-direction: column;
    }
    .product_extra-text h4 {
        font-size: 1.6rem;
    }
    .products_head-search {
        flex: none;
        width: 100%;
    }
}

@media (max-width: 640px) {
    .setting-section .third-col .check-item {
        width: 100%;
    }
    .products_head-brand-types {
        flex-wrap: wrap;
        gap: 1rem;
    }
    .products_head-brand-types .brand-type:last-child,
    .products_head-brand-types .brand-type:first-child {
        border-radius: 0.8rem;
    }
    .products_head-brand-types .brand-type:not(:first-child) {
        border-left: 1px solid;
        border-radius: 0.8rem;
    }
    .products_head-brand-types .brand-type {
        width: 100%;
        border-radius: 0.8rem;
    }
    .sidebar-hidden .pc {
        width: 100%;
    }
    .popup_content .swiper-button-prev {
        left: 0;
    }
    .popup_content .swiper-button-next {
        right: 0;
    }
    .product_info-buttons {
        flex-direction: column;
    }
    .popup-confirm .popup_content {
        padding: 3.2rem 2rem;
    }
    .popup-confirm .popup_content-footer {
        flex-direction: column;
    }
    .popup-confirm-remove .popup_content-footer .button.cancel {
        width: 100%;
    }
    .setting-section .category-form-input {
        flex-direction: column;
        gap: 0;
    }
    .personalize_selection {
        flex-direction: column;
        margin-top: 1.6rem;
    }
    .personalize_selection .pse_item {
        margin-top: 0;
    }
}

@media (max-width: 450px) {
    .invite-brand {
        padding: 0.1em 0.5em;
    }
    .menu_link {
        padding: 0.8rem 1rem;
    }
    .products_content {
        max-width: 100%;
    }

    aside.filters {
        position: absolute;
        left: 0;
        width: auto;
        z-index: 9;
        background: #fff;
    }
    aside.filters.hidden {
        position: relative;
        left: auto;
        width: 4.8rem;
    }
}

@media (max-width: 400px) {
    .header_action .icon,
    .dropdown .icon {
        width: 2rem;
        height: 2rem;
    }
    .header_actions {
        gap: 1rem;
    }
}
